@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
/* import Lato font */


/* Global styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Mobile first breakpoint 320px */

html,
body {
  font-family: 'Lato', sans-serif;
  height: 100%;
  min-width: 320px;
}

body {
  background: url('https://source.unsplash.com/iI1SSTbW218') no-repeat center center fixed;
  background-size: cover;

}
.NewTodoForm {
    height: 45px;
    width: 300px;
    margin: 10px auto;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    align-content: stretch ;
    background-color: rgba(112,112,112,0.9);
}

form > * {
    height: 35px;
}

.NewTodoForm-Label {
    font-size: 30px;
    line-height: 30px;
    width: 30px;
    color: white;
    text-align: center;
}

.NewTodoForm-Input {
    width: 220px;
    background-color: transparent;
    color: white;
    border: none;
    font-size: 14px;
    line-height: 14px;
    padding-left: 10px;
}
.NewTodoForm-Input:focus {
    outline: none;
}

.NewTodoForm-Input::-webkit-input-placeholder {
    font-size: 16px;
    line-height: 16px;
    color: white;
    border: none;
}

.NewTodoForm-Input:-ms-input-placeholder {
    font-size: 16px;
    line-height: 16px;
    color: white;
    border: none;
}

.NewTodoForm-Input::-ms-input-placeholder {
    font-size: 16px;
    line-height: 16px;
    color: white;
    border: none;
}

.NewTodoForm-Input::placeholder {
    font-size: 16px;
    line-height: 16px;
    color: white;
    border: none;
}

.NewTodoForm-Button {
    border: none;
    background-color: transparent;
    color: white;
    width: 30px;
    font-size: 18px;
    line-height: 20px;
}

.NewTodoForm-Button:focus {
    outline: none;
}

.submit-button:hover {
    cursor: pointer;
    opacity: 0.5;
}
.Todo {
    background-color: white;
    display: flex;
    justify-content: space-between;
    height: 50px;
    margin: 5px auto;
    align-items: center;
    border-radius: 5px;
    width: 300px;
}

.Todo-Check {
    width: 10%;
    text-align: center;
}
.Todo-Text {
    width: 70%;
    padding: 0px 5px;
    text-decoration: none;
    overflow: scroll;
}

/* Hide scrollbar for Chrome, Safar and Opera */
.Todo-Text {
    -ms-overflow-style: none;
  }

  /* Hide scrollbar for IE and Edge */

.Todo-Text::-webkit-scrollbar {
    display: none;
  }


.complete {
    text-decoration: line-through;
}

.Todo-Buttons {
    display: flex;
    justify-content: space-evenly;
    width: 20%;
}

.Todo-Buttons {
    display: flex;
    justify-content: space-evenly;
    width: 20%;
}

.edit-button:hover {
    color: #428bca;
    cursor: pointer;
}

.delete-button:hover {
    color: 	#d9534f;
    cursor: pointer;
}

.Todo-Check:hover {
    opacity: 0.5;
    cursor: pointer;
}
.EditTodoForm-Container {
    background-color: white;
    display: flex;
    justify-content: space-between;
    height: 50px;
    margin: 5px auto;
    align-items: center;
    border-radius: 5px;
    width: 300px;
}

.EditTodo-Check {
    width: 10%;
    text-align: center;
}

.EditTodoForm {
    width: 90%;
    height: 50px;
    display: flex;
    align-items: center;
    
}

.EditTodoForm-Input {
    padding: 0px 5px;
    overflow: scroll;
    text-decoration: none;
    border: none;
    width: 210px;
    height: 19px;
    font-size: 16px;
    font-family: 'Lato', sans-serif;
}

/* Hide scrollbar for Chrome, Safar and Opera */
.EditTodoForm-Input {
    -ms-overflow-style: none;
  }

  /* Hide scrollbar for IE and Edge */

.EditTodoForm-Input::-webkit-scrollbar {
    display: none;
  }

.EditTodoForm-Input:focus {
    outline: none;
}

.complete {
    text-decoration: line-through;
}

.EditTodoForm-Button {
    width: 60px;
    border: none;
    font-size: 16px;
}

.EditTodoForm-Button:focus {
    outline: none;
}

.EditTodoForm-Button:hover {
    color: #5cb85c;
    cursor: pointer;
}
.Todos {
    height: 420px;
    overflow-y: scroll;
}
.today {
    color: white;
    font-size: 20px;
    font-weight: 700;
    padding: 30px 0px 15px 10px;
    width: 300px;
    margin: 0px auto;
}
